.App {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  /* justify-content: space-between; */
  justify-content: flex-start;
}


#map {
  width: 100%;
  height: 100%;
  /* max-width: 1020px; */
  /* max-height: 575px; */
  /* border: 1px solid red; */
  /* margin: 10px; */
}

.leaflet-marker-icon {
  background: none;
  border: none;
}

/* ZONES */

.markerPopup-zone {
  height: 100%;
  opacity: 1;
}

.markerPopup-zone:hover > span {
  visibility: visible;
}

/* TEMPLES */

.markerPopup-temple {
  height: 100%;
}

.markerPopup-zone > span {
  background: linear-gradient(to left, rgba(10, 10, 10, 0.5) 90%, rgba(10, 10, 10, 0.0));
}

.markerPopup-zone > span, .markerPopup-temple > span {
  border-top: 1px solid #aaa;
  border-bottom: 1px solid #aaa;
  color: white;
  position: absolute;
  width: max-content;
}

.markerPopup-zone > img, .markerPopup-temple > img {
  position: absolute;
}

.size-16 > span {
  right: 8px;
  top: 0px;
  padding: 0px 10px;
  font-size: 0.65rem;
  font-weight: 600;
  color: #0d1216;
}

.size-32 > span {
  right: 26px;
  top: 6px;
  padding: 2px 10px;
  font-size: 0.7rem;
  /* font-weight: 400; */
}

.size-48 > span {
  /* display: none; */
  right: 42px;
  top: 14px;
  padding: 2px 10px;
  font-size: 0.7rem;
  font-weight: 600;
}

.size-16 > img {
  height: 16px;
  width: 16px;
}

.size-32 > img {
  height: 32px;
  width: 32px;
}

.size-48 > img {
  height: 48px;
  width: 48px;
}

.red {
  background: linear-gradient(to left, rgba(255, 90, 90, 0.9) 90%, rgba(10, 10, 10, 0.0));
}
.green {
  background: linear-gradient(to left, rgba(123, 255, 0, 0.9) 90%, rgba(10, 10, 10, 0.0));
}
.blue {
  background: linear-gradient(to left, rgba(69, 150, 255, 0.9) 90%, rgba(10, 10, 10, 0.0));
}
.yellow {
  background: linear-gradient(to left, rgba(255, 251, 0, 0.9) 90%, rgba(10, 10, 10, 0.0));
}
.purple {
  background: linear-gradient(to left, rgba(204, 75, 255, 0.9) 90%, rgba(10, 10, 10, 0.0));
}
.orange {
  background: linear-gradient(to left, rgba(255, 102, 0, 0.9) 90%, rgba(10, 10, 10, 0.0));
}

header {
  background: rgb(13, 18, 22);
  color: #fff;
  width: 100%;
  height: 40px;
  display: flex;
  align-items: center;
}

nav > a {
  padding: 5px 10px;
  border-right: 1px solid #555;
}

nav > a:last-child {
  border: none;
}

nav > a:hover {
  color: #ddd;
}

h1 {
  text-align: center;
}

.App > span, .App > ul {
  /* display: flex; */
  flex-direction: column;
  align-items: center;

  background-color: #0d1216;
  border-radius: 4px;
  padding: 10px;
  
  position: absolute;
  z-index: 999;
  font-size: 0.9rem;
}

.App > span {
  top: 50px;
}

.App > ul {
  list-style: none;
  bottom: 50px;
}

.App > ul > li {
  padding: 5px;
}

footer {
  font-size: 0.8rem;
}
